@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400&display=swap);
:root {
  --color-primary: #B1030E;
  --color-secondary: #F24957;
  --color-accent: #B9E1F6;
  --color-background: #FAF6EF;
  --color-text: #0D0D0D;
}

body {
  font-family: 'Roboto Mono', monospace;
  font-weight: 300;
  background-color: var(--color-background);
  color: var(--color-text);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto Mono', monospace;
  font-weight: 400;
  color: var(--color-primary);
}

a {
  color: var(--color-secondary);
}

a:hover {
  color: var(--color-primary);
}

.accent-bg {
}
.homepage {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

nav ul {
  display: flex;
  list-style-type: none;
}

nav ul li {
  margin-left: 20px;
}

section {
  margin: 60px 0;
  padding: 40px;
  border-radius: 8px;
}

.accent-bg {
  background-color: var(--color-accent);
}

#hero {
  text-align: center;
  padding: 80px 0;
}

#hero h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

#services ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 20px;
  gap: 20px;
  padding: 0;
  list-style-type: none;
}

#services li {
  background-color: var(--color-secondary);
  color: var(--color-background);
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

#contact form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin: 0 auto;
}

#contact input,
#contact textarea {
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid var(--color-text);
  border-radius: 4px;
  font-family: 'Roboto Mono', monospace;
}

#contact button {
  background-color: var(--color-primary);
  color: var(--color-background);
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'Roboto Mono', monospace;
  font-weight: 400;
}

#contact button:hover {
  background-color: var(--color-secondary);
}

footer {
  text-align: center;
  padding: 20px 0;
  margin-top: 40px;
  border-top: 1px solid var(--color-text);
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 20px;
  gap: 20px;
  padding: 20px 0;
}

.service-button {
  background-color: var(--color-secondary);
  color: var(--color-background);
  border: none;
  padding: 20px;
  font-size: 1em;
  font-family: 'Roboto Mono', monospace;
  font-weight: 400;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 8px;
}

.service-button:hover {
  background-color: var(--color-primary);
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

/* Responsive design */
@media (max-width: 768px) {
  .services-grid {
    grid-template-columns: 1fr;
  }
}

.service-details {
  margin-top: 20px;
  padding: 20px;
  background-color: var(--color-accent);
  border-radius: 8px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

